.dropdown.show {
    display: block;
}

.dropdown {
    right: 0;
    left: auto;
    box-shadow: 0 10px 15px -3px rgba(46, 41, 51, 0.08),
    0 4px 6px -2px rgba(71, 63, 79, 0.16);
    font-size: 0.875rem;
    z-index: 9999;
    min-width: 10rem;

    padding: 0.5rem 0;

    background-color: #e29c5d;
    list-style: none;
    display: none;
}

@media screen and (min-width: 44rem) and (prefers-reduced-motion: no-preference) {

    .dropdown {
        position: absolute;
    }

}

